<template>
  <div>
    <div class="clearfix">
      <router-link class="ui right floated primary button" to="/node/group"><i class="cubes icon"></i> </router-link>
      <div class="ui label" title="/"><i class="cube icon"></i> </div>
      <div class="ui label" title=""><i class="green cube icon"></i> </div>
      <div class="ui label" title=""><i class="red cube icon"></i> </div>
    </div>
    <h4 class="ui horizontal divider header"> {{count}}</h4>
    <div v-for="node in nodes" class="ui label"><i v-bind:class="{green: node.alived && node.connected, red: node.alived && !node.connected}" class="cube icon"></i> {{node.id}}</div
  </div>
</template>

<script>
export default {
  name: 'node',
  data: function(){
    return {
      count: 0,
      nodes: []
    }
  },

  mounted: function(){
    var vm = this;
    this.$rest.GET('nodes').onsucceed(200, (resp)=>{
      vm.nodes = resp;
      vm.count = resp.length;
    }).do();
  }
}
</script>


